<template>
  <el-tooltip
    content=""
    :placement="TooltipPlacement"
  >
    <el-select
      filterable
      v-model="Value"
      :placeholder="placeholder"
      collapse-tags
      :multiple="multiple"
      @change="ChangeHandle"
      remote
      clearable
      :size="size"
      :remote-method="remoteMethod"
      reserve-keyword
      :loading="loading"
    >
      <el-option
        v-for="item in Datas"
        :key="item.DisciplineID"
        :label="item.DisciplineName"
        :value="item.DisciplineID"
      ></el-option>
    </el-select>
  </el-tooltip>
</template>

<script>
import api from "@/scripts/api";

export default {
  props: {
    size: {
      type: String,
      default: "mini"
    },
    TooltipPlacement: {
      type: String,
      default: "right"
    },
    /**
     * 
     */
    multiple: {
      type: Boolean,
      default: false
    },
    value: {},
    placeholder: {
      type: String,
      default: ""
    }
  },
  watch: {
    async value(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.loading = true;
        const res = await api.Discipline.GetDataByName(
          "",
          this.multiple ? JSON.stringify(newVal) : `["${newVal}"]`
        );
        if (res) {
          this.Datas = res.Datas;
          this.Value = newVal;
        }
        this.loading = false;
      }
    }
  },
  data() {
    return { loading: false, Value: this.value, Datas: [] };
  },
  mounted() {
    this.LoadData();
  },
  methods: {
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        this.LoadData(query);
      } else {
        this.Datas = [];
      }
    },
    ChangeHandle(val) {
      this.$emit("update:value", val);
      const findC = this.Datas.find(c => c.DisciplineID === val);
      if (findC) this.$emit("update:text", findC.DisciplineName);
      const LZString = require("lz-string");
      let result = "";
      if (val && this.multiple) {
        const jsonStr = JSON.stringify(val);
        if (jsonStr) {
          result = LZString.compressToBase64(
            jsonStr.slice(1, jsonStr.length - 1)
          );
        }
      } else {
        result = LZString.compressToBase64(val);
      }
      this.$emit("Filter", result);
    },
    async LoadData(name) {
      const res = await api.Discipline.GetDataByName(
        name,
        this.multiple ? JSON.stringify(this.Value) : `["${this.Value}"]`
      );
      if (res) {
        this.Datas = res.Datas;
      }
      this.loading = false;
    }
  }
};
</script>

<style lang="less"></style>
