<template>
  <div class="vb" :class="{ IsLive: IsLive }">
    <div class="Player" ref="videoBox">
      <Player
        :IsLive.sync="IsLive"
        ref="Player"
        :autoplay="true"
        :source="CurrentURL"
        :type="Type"
        :IsMuted="false"
        :IsPaused.sync="IsPaused"
        :duration.sync="Duration"
        @CurrentTime="CurrentTimeChange"
        @PlayEnded="PlayEnded"
        @dblclick="FullScreenHandler"
      />
      <!-- <div v-if="Videos && Videos.length > 0" :class="'vw_'+Videos.length">
        <div :id="'player'+(index + 1)" v-for="(video,index) in Videos" :key="index">
          <Player
            v-if="index === 0"
            ref="Player"
            @click="Swap"
            :autoplay="true"
            :source="`${VODURL_Prefix}${ IsLive ? '' : (video.MaterialFileDirectory ? '/record' : '/vod')}${video.MaterialStorePath}${VODURL_Suffix}`"
            :type="Type"
            :IsMuted="index!==0"
            :IsPaused.sync="IsPaused"
            :duration.sync="Duration"
            @CurrentTime="CurrentTimeChange"
          />
          <Player
            v-else
            ref="Player"
            @click="Swap"
            :autoplay="true"
            :source="`${VODURL_Prefix}${video.MaterialStorePath}${VODURL_Suffix}`"
            :type="Type"
            :IsMuted="index!==0"
          />
        </div>
      </div>-->
      <div class="controlbar">
        <div class="controlbar-bg"></div>
        <el-slider
          v-if="!IsLive"
          class="progress"
          v-model="progress"
          :min="0"
          :max="Duration"
          @change="SetProcess"
          @mousedown.native="progressMousedownHandler"
          @mouseup.native="progressMouseupHandler"
          :format-tooltip="FormatDuration"
        ></el-slider>
        <el-tooltip content="" placement="top">
          <div
            class="play-btn iconfont icon_Play"
            v-if="IsPaused"
            @click="Play"
          ></div>
        </el-tooltip>
        <el-tooltip content="" placement="top">
          <div
            class="play-btn iconfont icon_pause_1"
            v-if="!IsPaused"
            @click="Pause"
          ></div>
        </el-tooltip>
        <div class="time-display" v-if="!IsLive">
          <span class="current-time" style="display: inline">{{
            FormatDuration(progress)
          }}</span>
          <span class="time-bound" style="display: inline">/</span>
          <span class="duration" style="display: inline">{{
            FormatDuration(Duration)
          }}</span>
        </div>
        <el-tooltip
          :content="!isFullScreen ? '' : ''"
          placement="top"
        >
          <div
            class="fullscreen-btn iconfont"
            @click="FullScreenHandler"
            :class="{
              icon_fullscreen: !isFullScreen,
              icon_exit_fullscreen: isFullScreen,
            }"
          ></div>
        </el-tooltip>
        <el-popover
          popper-class="volume-control"
          trigger="hover"
          placement="top"
        >
          <el-slider v-model="volume" vertical @change="SetVolume"></el-slider>
          <div
            slot="reference"
            class="volume iconfont"
            :class="{ icon_volume: volume > 0, icon_mute: volume <= 0 }"
            @click="ChangeMuted"
          ></div>
        </el-popover>
        <el-radio-group
          v-model="PlayIndex"
          size="mini"
          @change="ChangePlayIndex"
          v-if="IsLive && Videos !== null && Videos.length > 1"
        >
          <el-radio-button
            :label="index"
            v-for="(video, index) in Videos"
            :key="index"
            >{{ video.Name }}</el-radio-button
          >
        </el-radio-group>
      </div>
    </div>
    <div
      class="list"
      :class="{ contract: CanExpand && !IsExpand }"
      v-if="!IsLive && ShowPlayList"
    >
      <el-tooltip :content="!IsExpand ? '' : ''" placement="top">
        <i
          :class="{ 'el-icon-s-fold': !IsExpand, 'el-icon-s-unfold': IsExpand }"
          v-if="CanExpand"
          @click="IsExpand = !IsExpand"
        />
      </el-tooltip>
      <div class="list-box">
        <el-checkbox
          @change="ChangeAllChecked"
          v-model="CheckedALL"
          v-if="SelectAble && ShowThumb"
          ></el-checkbox
        >
        <div
          class="playitem"
          :class="{
            thumb: ShowThumb,
            UnChecked: !SelectAble,
            Checked: item.Checked,
            active: CurrentIndex === index,
          }"
          v-for="(item, index) in PlayList"
          :key="index"
        >
          <div @click="ChangePlay(item, index)" v-if="!ShowThumb">
            <i class="el-icon-film">{{ item.FileName }}.mp4</i>
            <span>{{ item.Duration | FormatDuration }}</span>
          </div>
          <el-avatar
            :size="30"
            @click.native="Download(item)"
            v-if="!ShowThumb"
          >
            <i class="el-icon-download" />
          </el-avatar>
          <img
            :src="item.imgURL"
            fit="fit"
            v-if="ShowThumb"
            @click="ChangeChecked(item, index)"
            @dblclick="ChangePlay(item, index)"
          />
          <span
            v-if="ShowThumb"
            @dblclick="ChangePlay(item, index)"
            @click="ChangeChecked(item, index)"
            >{{ item.FileName }}</span
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import screenfull from "screenfull";
import moment from "moment";
import { PrefixIntrger } from "@/scripts/utils";
import store from "@/scripts/store";
export default {
  props: {
    IsLive: { type: Boolean, default: false },
    Videos: { type: Array, default: () => [] },
    ShowPlayList: { type: Boolean, default: true },
    ShowThumb: { type: Boolean, default: false },
    SelectAble: { type: Boolean, default: true },
    CanExpand: { type: Boolean, default: false },
  },
  watch: {
    Videos(newVal, oldVal) {
      this.LoadPlayList();
      // if (newVal == null || newVal.length <= 0) {
      //   // this.Dispose();
      // } else {
      //   this.CurrentURL = `${this.VODURL_Prefix}${
      //     this.Videos[0].MaterialFileDirectory ? "/record" : "/vod"
      //   }${this.Videos[0].MaterialStorePath}${this.VODURL_Suffix}`;
      // }
    },
  },
  components: {
    Player: () => import("@/components/Player/VPPlayer.vue"),
  },
  data() {
    return {
      CheckedALL: true,
      PlayIndex: 0,
      HasThcRTSP: false,
      HasStuRTSP: false,
      PlayList: [],
      CurrentURL: "",
      CurrentIndex: 0,
      Type: !this.IsLive ? "mp4" : "flv",
      VODURL_Prefix: "",
      VODURL_Suffix: "",
      IsLoaded: false,
      progress: 0,
      volume: 100,
      beforeMuted: 100,
      IsPaused: false,
      Duration: 0,
      ProgressIsDrag: false,
      IsExpand: false,
      isFullScreen: false, // 
    };
  },
  mounted() {
    screenfull.on("change", () => {
      this.isFullScreen = screenfull.isFullscreen;
    });
    this.VODURL_Prefix = `http://${this.$root.SrvConfigInfo.VODHost}:${this.$root.SrvConfigInfo.VODPort}`;
    this.LoadPlayList();
    // if (this.Videos && this.Videos.length > 0) {
    //   let vodDir = "";
    //   this.Type = "mp4";
    //   this.VODURL_Suffix = "";
    //   switch (this.$root.SrvConfigInfo.VODMode) {
    //     case "1": {
    //       vodDir = "";
    //       break;
    //     }
    //     case "2": {
    //       vodDir = "/hls-vod";
    //       this.Type = "m3u8";
    //       this.VODURL_Suffix = ".m3u8";
    //     }
    //   }

    //   this.VODURL_Prefix = `http://${this.$root.SrvConfigInfo.VODHost}:${this.$root.SrvConfigInfo.VODPort}${vodDir}`;
    //   this.CurrentURL = `${this.VODURL_Prefix}${
    //     this.Videos[0].MaterialFileDirectory ? "/record" : "/vod"
    //   }${this.Videos[0].MaterialStorePath}${this.VODURL_Suffix}`;
    // } else {
    //   this.CurrentURL = "";
    // }
  },
  beforeDestroy() {
    screenfull.off("change");
    this.Dispose();
  },
  methods: {
    async ChangeAllChecked() {
      const checkeds = [];
      await this.PlayList.forEach((element) => {
        element.Checked = this.CheckedALL;
        if (element.Checked) checkeds.push(element.MaterialID);
      });
      this.$emit("update:checked", checkeds);
    },
    ChangePlayIndex() {
      this.ChangePlay(this.Videos[this.PlayIndex], this.PlayIndex);
    },
    async ChangeChecked(item, index) {
      if (this.SelectAble) {
        item.Checked = !item.Checked;
        const checkeds = [];
        await this.PlayList.forEach((element) => {
          if (element.Checked) checkeds.push(element.MaterialID);
        });
        this.$emit("update:checked", checkeds);
      } else {
        this.ChangePlay(item, index);
      }

      this.CheckedALL = this.PlayList.findIndex((item) => !item.Checked) < 0;
    },
    Download(row) {
      const ConfigInfo = store.GetConfigInfo();
      const FileSaver = require("file-saver");
      FileSaver.saveAs(
        `http://${ConfigInfo.Host}:${ConfigInfo.Port}/FJRH.Campus/MaterialService/Download?mid=${row.MaterialID}`
      );
    },
    PlayEnded() {
      if (this.CurrentIndex < this.PlayList.length - 1) {
        this.CurrentIndex++;
        this.ChangePlay(this.PlayList[this.CurrentIndex], this.CurrentIndex);
      }
    },
    ChangePlay(item, index) {
      this.CurrentIndex = index;
      if (this.IsLive) {
        this.CurrentURL = `${item.Path}`;
      } else {
        this.CurrentURL = `${this.VODURL_Prefix}/vod${item.Path}${this.VODURL_Suffix}`;
      }
      // console.log(this.CurrentURL);
    },
    // 
    async LoadPlayList() {
      this.PlayList = [];
      const checkeds = [];
      let nto1s = 1;
      let thcs = 1;
      let stus = 1;
      if (this.Videos) {
        await this.Videos.forEach((element, index) => {
          this.$set(element, "Checked", this.SelectAble);
          checkeds.push(element.MaterialID);
          this.$set(
            element,
            "imgURL",
            `${this.$root.DownloadURL}?filename=${element.MaterialID}&fileType=9`
          );
          let FileName = "";
          switch (element.FrameType) {
            case "9": {
              FileName = `/_${nto1s}`;
              nto1s++;
              break;
            }
            case "1": {
              FileName = `_${thcs}`;
              thcs++;
              break;
            }
            case "2": {
              FileName = `_${stus}`;
              stus++;
              break;
            }
          }
          this.$set(element, "FileName", FileName);
          this.PlayList.push(element);
          const pIndex = this.$route.params.index
            ? this.$route.params.index
            : 0;
          if (index === pIndex) {
            this.ChangePlay(element, index);
          }
          this.$emit("update:checked", checkeds);
        });
      }
    },
    // 
    FullScreenHandler() {
      if (screenfull.isEnabled) {
        screenfull.toggle(this.$refs.videoBox);
      }
    },
    // 
    ChangeMuted() {
      if (this.volume > 0) {
        this.beforeMuted = this.volume;
        this.volume = 0;
      } else {
        this.volume = this.beforeMuted;
      }

      this.SetVolume(this.volume);
    },
    // 
    SetVolume(val) {
      if (this.$refs.Player) {
        this.$refs.Player.Volume(val);
      }
    },
    // 
    FormatDuration(val) {
      const duration = moment.duration(val, "seconds");
      return `${PrefixIntrger(duration.hours(), 2)}:${PrefixIntrger(
        duration.minutes(),
        2
      )}:${PrefixIntrger(duration.seconds(), 2)}`;
    },
    progressMousedownHandler() {
      this.ProgressIsDrag = true;
    },
    progressMouseupHandler() {
      this.ProgressIsDrag = false;
    },
    // 
    CurrentTimeChange(val) {
      if (!this.ProgressIsDrag) this.progress = val;
    },
    // 
    SetProcess(val) {
      if (this.$refs.Player) {
        this.$refs.Player.CurrentTime(val);
      }
    },
    // 
    Play() {
      if (this.$refs.Player) {
        this.$refs.Player.Play();
      }
    },
    // 
    Pause() {
      if (this.$refs.Player) {
        this.$refs.Player.Pause();
      }
    },
    // 
    Dispose() {
      try {
        if (this.$refs.Player) {
          console.log("");
          try {
            this.$refs.Player.Pause();
          } catch (ex) {}
          this.$refs.Player.Dispose();
        }
      } catch (ex) {
        console.log(ex);
      }
      this.progress = 0;
      this.Duration = 0;
    },
  },
};
</script>

<style lang="less">
.vb {
  width: 100%;
  height: 100%;
  position: relative;
  @BorderColor: rgba(0, 0, 0, 0.75);

  .el-carousel {
    margin: 0px !important;
    .el-carousel__container {
      height: 100%;
      overflow: hidden;

      .el-carousel__item {
        width: 100% !important;
        height: 100%;
        left: 0px !important;
        background-color: transparent !important;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }

  > div {
    width: 100%;
    height: 100%;
    position: relative;
  }

  .controlbar {
    // display: none;
    position: absolute;
    left: 0px;
    bottom: 0px;
    width: 100%;
    height: 44px;
    z-index: 2;

    .controlbar-bg {
      background: rgba(0, 0, 0, 0.5);
      width: 100%;
      height: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: -1;
    }

    .el-slider.progress {
      bottom: 40px !important;

      .el-slider__runway {
        margin: 0px;
        margin-left: 5px;
        width: calc(100% - 10px);
        height: 4px;
        background: rgba(195, 197, 198, 0.5);
        cursor: pointer;

        .el-slider__bar {
          height: 100%;
        }

        .el-slider__button-wrapper {
          width: 16px;
          height: 16px;
          border-radius: 8px;
          top: -8px;

          .el-slider__button {
            width: 10px;
            height: 10px;
          }
        }
      }
    }

    .iconfont {
      cursor: pointer;
      color: #ffffff;
      width: 24px;
      height: 24px;
    }

    .play-btn {
      float: left;
      margin-left: 15px;
      margin-top: 12px;
    }

    .time-display {
      float: left;
      margin-left: 10px;
      height: 40px;
      line-height: 40px;
      color: #a4aab3;
      font-size: 12px;

      .current-time {
        color: #fff;
      }
    }

    .fullscreen-btn {
      float: right;
      margin-right: 10px;
      margin-top: 12px;
    }

    .el-radio-group {
      float: right;
      margin-right: 10px;
      margin-top: 8px;
    }

    .volume {
      float: right;
      margin-right: 5px;
      margin-top: 10px;
    }
  }

  @ListWidth: 230px;
  .Player {
    float: left;
    width: calc(100% - @ListWidth);
    height: 100%;
    overflow: hidden;
  }

  .list {
    @BorderColor: rgba(195, 197, 198, 0.25);
    float: left;
    border-left: 1px solid @BorderColor;
    width: @ListWidth - 1px;
    overflow: auto;

    .el-icon-s-fold,
    .el-icon-s-unfold {
      color: #fff;
      position: absolute;
      top: 50%;
      margin-top: -20px;
      margin-left: -20px;
      cursor: pointer;
      font-size: 20px;
      background-color: rgba(0, 0, 0, 0.5);
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      border-radius: 20px;
      display: block;
      z-index: 2;
    }

    .list-box {
      height: 100%;
      overflow-y: auto;

      .el-checkbox {
        margin-left: 8px;
      }

      .playitem {
        width: 100%;
        height: 80px;
        border-bottom: 1px solid @BorderColor;
        color: #fff;
        font-size: 14px;

        img {
          width: calc(100% - 4px);
          height: 113.63px;
        }

        > div {
          float: left;
          padding: 15px 20px;
          width: calc(100% - 20px * 2 - 50px);

          > * {
            width: 100%;
            line-height: 25px;
          }
          i::before {
            margin-right: 5px;
          }
        }

        .el-avatar {
          cursor: pointer;
          float: right;
          margin-right: 20px;
          margin-top: 25px;
          background-color: @BorderColor;

          i {
            cursor: pointer;
            font-size: 16px;
          }
        }
      }

      .playitem.thumb.Checked {
        border: #fd4f4f 3px solid;
      }

      .playitem.thumb {
        overflow: hidden;
        position: relative;
        width: calc(100% - 16px);
        height: auto;
        margin: 8px;
        border-bottom: none;
        border: 3px solid transparent;
        background-color: #000000;

        img {
          opacity: 0.5;
        }

        > span {
          position: absolute;
          font-size: 18px;
          font-weight: 700;
          width: 100%;
          top: 50%;
          left: 0px;
          text-align: center;
          margin-top: -25px;
        }
      }
      .playitem.thumb:hover {
        img {
          opacity: 0.8;
        }
      }

      .playitem.thumb.UnChecked {
        border: none;
        width: calc(100% - 16px);
      }

      .playitem.thumb.active {
        background-color: #000000;
        img {
          opacity: 1;
        }

        > span {
          color: rgba(255, 255, 255, 0.5);
        }
      }

      .playitem.active {
        background-color: @ThemeColor;
        .el-avatar {
          background-color: #fff;
          color: @ThemeColor;
        }
      }
    }
  }

  .list.contract {
    width: 0px !important;
  }
}
.IsLive {
  .Player {
    width: 100%;
  }
}

.vb:hover {
  .controlbar {
    display: block;
  }
}

.volume-control {
  @bgColor: rgba(60, 60, 60, 0.5);
  background: @bgColor;
  padding: 0px;
  padding-bottom: 16px;
  width: 38px;
  min-width: auto;
  border-color: rgba(255, 255, 255, 0.5);

  .popper__arrow,
  .popper__arrow::after {
    border-top-color: rgba(255, 255, 255, 0.5) !important;
  }

  .el-slider.is-vertical {
    height: 130px;
    width: 38px;

    .el-slider__runway {
      margin-top: 14px;
      height: calc(100% - 14px);
      .el-slider__button-wrapper {
        .el-slider__button {
          width: 14px;
          height: 14px;
          border: none;
        }
      }
    }
  }
}
</style>>