<template>
  <div class="weekCal" v-loading="Loading">
    <el-card class="range" shadow="hover">
      <i class="el-icon-arrow-left" @click="PreWeek" />
      <div>
        <div class="fake-week">{{dtStart}} - {{dtEnd}}</div>
        <el-date-picker
          :picker-options="pickerOptions"
          v-model="weekDate"
          type="week"
          size="mini"
          :editable="false"
          :clearable="false"
          @change="ChangeWeekRange"
        ></el-date-picker>
      </div>
      <i class="el-icon-arrow-right" @click="NextWeek" />
    </el-card>
    <div class="weeks">
      <el-card
        :shadow="day.date.isSame(weekDate, 'day') ? 'always' : 'hover'"
        v-for="day in days"
        :key="day.text"
        @click.native="ChangeDay(day)"
      >
        <el-tooltip
          class="item"
          effect="light"
          :content="Taskcontent"
          placement="right-start"
          :disabled="Taskdisabled"
        >
          <el-badge :value="day.count" type="primary" :max="999" :hidden="Concise">
            <div class="week">{{day.text}}</div>
            <div class="day" v-if="!Concise">{{day.day}}</div>
          </el-badge>
        </el-tooltip>
      </el-card>
    </div>
  </div>
</template>

<script>
import moment from "moment";
moment.locale("zh-cn");
export default {
  props: {
    // 
    Loading: {
      type: Boolean,
      default: false
    },
    // 
    value: {
      type: String,
      default: ""
    },
    Taskcont: {
      type: String,
      default: ""
    },
    Taskdis: {
      type: Boolean,
      default: true
    },
    // 
    Concise: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    Taskcont(val) {
      this.Taskcontent = val;
    },
    Taskdis(val) {
      this.Taskdisabled = val;
    }
  },
  data() {
    return {
      Taskcontent: this.Taskcont,
      Taskdisabled: this.Taskdis,
      dtStart: null,
      dtEnd: null,
      pickerOptions: {
        firstDayOfWeek: 1
      },
      weekDate: this.value,
      days: []
    };
  },
  mounted() {
    this.weekDate = moment();
    this.CalOrgin();
  },
  methods: {
    /**
     * 
     */
    CalOrgin() {
      const weekOfday = moment(this.weekDate).format("E"); // 
      this.dtStart = moment(this.weekDate)
        .subtract(weekOfday - 1, "days")
        .format("YYYYMMDD"); // 
      this.dtEnd = moment(this.weekDate)
        .add(7 - weekOfday, "days")
        .format("YYYYMMDD"); // 
      this.days = [];
      for (let i = 0; i < 7; i++) {
        const date = moment(this.dtStart, "YYYYMMDD").add(i, "days");
        this.days.push({
          date: date,
          text: this.Concise ? date.format("dd") : date.format("dddd"),
          day: date.format("YYYYMMDD"),
          shadow: date.isSame(this.weekDate, "day") ? "always" : "hover",
          count: 0
        });
      }

      this.$emit("update:value", this.weekDate.format("YYYY-MM-DD"));
      this.$emit(
        "ValueChanged",
        this.weekDate.format("YYYY-MM-DD"),
        [
          moment(this.dtStart, "YYYYMMDD").format("YYYY-MM-DD"),
          moment(this.dtEnd, "YYYYMMDD").format("YYYY-MM-DD")
        ],
        dayCouts => this.BuildCount(dayCouts)
      );
    },
    LoadCount() {
      this.$emit(
        "ValueChanged",
        null,
        [
          moment(this.dtStart, "YYYYMMDD").format("YYYY-MM-DD"),
          moment(this.dtEnd, "YYYYMMDD").format("YYYY-MM-DD")
        ],
        dayCouts => this.BuildCount(dayCouts)
      );
    },
    /**
     * 
     */
    BuildCount(dayCouts) {
      if (dayCouts) {
        dayCouts.forEach(day => {
          const findDay = this.days.find(d => d.date.isSame(day.CSDate, "day"));

          if (findDay) {
            findDay.count = day.Count;
          }
        });
      }
    },
    /**
     * 
     */
    ChangeWeekRange() {
      this.weekDate = moment(this.weekDate);
      this.CalOrgin();
    },
    /**
     * 
     */
    PreWeek() {
      this.weekDate = moment(this.weekDate).add(-1, "weeks");
      this.CalOrgin();
    },
    /**
     * 
     */
    NextWeek() {
      this.weekDate = moment(this.weekDate).add(1, "weeks");
      this.CalOrgin();
    },
    /**
     * 
     */
    ChangeDay(dayInfo) {
      this.weekDate = dayInfo.date;
      this.$emit("update:value", this.weekDate.format("YYYY-MM-DD"));
      this.$emit("ValueChanged", this.weekDate.format("YYYY-MM-DD"));
    }
  }
};
</script>

<style lang="less">
.weekCal {
  margin-top: @Margin;

  .range {
    @height: 35px;

    .el-card__body {
      height: @height;
      padding: 0px @Margin / 2;
      width: calc(100% - @Margin);
      display: flex;
      justify-content: space-between;
      align-items: center;

      > i {
        width: @Margin;
        height: @Margin;
        line-height: @Margin;
        text-align: center;
        border-radius: @Margin;
        border: 1px solid rgba(0, 0, 0, 0.5);
        cursor: pointer;
      }

      > div {
        overflow: hidden;
        height: 100%;
        line-height: @height;

        .el-date-editor {
          height: 100%;
          display: block;
          margin-top: 0 - @height;
          opacity: 0;

          .el-input__inner {
            border: none;
          }
        }
      }
    }
  }

  .weeks {
    margin-top: @Margin / 2;
    display: flex;
    justify-content: space-between;
    border: 1px solid #ebeef5;

    .el-card {
      width: 100%;
      display: flex;
      text-align: center;
      border: none;

      .el-card__body {
        width: 100%;
        padding: 10px 15px;

        .el-badge {
          width: 100%;

          .day {
            color: #666666;
          }

          .week {
            color: #333333;
          }

          .el-badge__content {
            width: 30px;
            right: 35px;
            top: 5px;
          }
        }
      }
    }
  }
  @media (min-width: 1000px) and (max-width: 1400px) {
    .weeks .el-card .el-card__body {
      padding: 8px;
    }
  }
}
</style>