<template>
  <div id="class-screen">
    <div class="content">
      <div class="head">
        <div class="mb">
          <div class="mb_title">
            <img :src="getPicture(cpdata.SSContent)" alt/>
          </div>
          <div class="mb">
            
            <span class="primary">{{cpdata.SSAnswer}}</span>
          </div>
        </div>

        <div class="part_title"></div>
        <div class="part_line">
          <el-row v-if="subjectType !== 4" style="min-height: 200px;">
            <el-col :span="10">
              <r-chart ref="rchart" title="" :data="answerStatistics" @chartChange="selectChart">
              </r-chart>
            </el-col>
            <el-col :span="14">
              <person-card title="" :data="answerPersons" :subject-type="subjectType" :picture-type="6">
              </person-card>
            </el-col>
          </el-row>
          <el-row v-else style="min-height: 100px;">
            <el-col :span="24">
              <person-card ref="Appraise" showDetail title="" :data="answerList" :subject-type="subjectType"
                           @submitSave="saveAppraise" :picture-type="6">
              </person-card>
            </el-col>
          </el-row>
        </div>

        <div class="part_title"></div>
        <div class="part_line">
          <el-row>
            <el-col :span="4" class="line_left"></el-col>
            <el-col :span="16">
              <el-progress :percentage="surveyInfo.yesPer||0" :text-inside="true" :stroke-width="24" status="success">
              </el-progress>
            </el-col>
            <el-col :span="4" class="line_right">{{surveyInfo.yes}}</el-col>
          </el-row>
          <el-row>
            <el-col :span="4" class="line_left"></el-col>
            <el-col :span="16">
              <el-progress :percentage="surveyInfo.noPer||0" :text-inside="true" :stroke-width="24" status="exception">
              </el-progress>
            </el-col>
            <el-col :span="4" class="line_right">{{surveyInfo.no}}</el-col>
          </el-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/scripts/api";
import RChart from "./RChart";
import PersonCard from "./PersonCard";
import { ModulePicture, practiceTypeName } from "@/scripts/dictionary";

export default {
  components: { RChart, PersonCard },
  props: {
    cpdata: {
      type: Object,
      default: () => {
        return {};
      }
    },
    subjectId: {
      type: String,
      default: ""
    }
  },
  data () {
    return {
      // cpdata: JSON.parse(sessionStorage.getItem("T_ScreenQuestion")),
      psdata: Object,
      // subjectId: this.$route.params.id,
      subjectAnswer: "",
      subjectType: "",
      answerStatistics: [],
      answerPersons: [],
      answerList: [],
      surveyInfo: {}
    };
  },
  created () {
    this.subjectType = this.cpdata["SSType"];
    if (this.subjectType !== 4) {
      this.getAnswerStatistics();
    } else {
      this.getAnswerList();
    }
    this.getSurveyList();
  },
  methods: {
    getAnswerStatistics () {
      this.answerStatistics = [];
      api.IScreenSubject.DataAnswerStatistics(this.subjectId).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          let total = 0;
          res.Datas.forEach(r => {
            total += r["Total"] || 0;
          });
          let active = -1;
          res.Datas.forEach((item, k) => {
            let per = 0;
            if (total !== 0) {
              per =
                Math.round((10000.0 * (item["Total"] || 0)) / total) / 100;
            }
            const sub = {
              name: item["OptionName"],
              value: item["Total"],
              percentage: per
            };
            this.answerStatistics.push(sub);
            if (active === -1 && (item["Total"] || 0) > 0) active = k;
          });
          if (active > -1 && this.subjectType !== 4) {
            this.$refs["rchart"].selectChart(
              this.answerStatistics[active],
              active
            );
          }
        }
      }).catch(() => {
      });
    },
    getAnswerDetail () {
      this.answerPersons = [];
      api.IScreenSubject.DataAnswerInfo(
        this.subjectId,
        this.subjectAnswer
      ).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            const sub = {
              id: item.Key["StudentID"],
              userID: item.Key["UserID"],
              name: item.Key["StudentName"],
              gender: item.Key["Gender"],
              no: item.Key["Sno"]
            };
            this.answerPersons.push(sub);
          });
        }
      }).catch(() => {
      });
    },
    getAnswerList () {
      this.answerList = [];
      api.IScreenSubject.DataAnswerMember(this.subjectId).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            const sub = {
              id: item.Key["StudentID"],
              userID: item["UserID"],
              name: item["StudentName"],
              time: item.Key["SubmitTime"],
              score: item.Key["SAScore"],
              type: item.Key["SAForm"],
              answerId: item.Key["SAID"],
              content: item.Key["SAContent"],
              comment: item.Key["Comment"]
            };
            this.answerList.push(sub);
          });
        }
      }).catch(() => {
      });
    },
    getSurveyList () {
      this.surveyInfo = {
        yes: 0,
        yesPer: 0,
        no: 0,
        noPer: 0,
        total: 0
      };
      api.IScreenSubject.DataASurveyStatistics(this.subjectId).then(res => {
        if (res.Result) {
          this.surveyInfo = {
            yes: res["YesMaster"] || 0,
            yesPer: 0,
            no: res["NoMaster"] || 0,
            noPer: 0,
            total: 0
          };
          this.surveyInfo.total = this.surveyInfo.yes + this.surveyInfo.no;
          this.surveyInfo.yesPer = Math.round((10000.0 * this.surveyInfo.yes) / this.surveyInfo.total) / 100;
          this.surveyInfo.noPer = Math.round((10000.0 * this.surveyInfo.no) / this.surveyInfo.total) / 100;
        }
      }).catch(() => {
      });
    },
    // 
    fetchSSType (val) {
      return practiceTypeName(val);
    },
    selectChart (index) {
      this.subjectAnswer = this.answerStatistics[index].name;
      this.getAnswerDetail();
    },
    saveAppraise (form) {
      this.$refs["Appraise"].btnLoading = true;
      api.IScreenSubject.DataScoreSave(
        form.answerId,
        form.score,
        form.comment
      ).then(res => {
        if (res.Result) {
          this.$message.success("");
          const answer = this.answerList.find(
            r => r.answerId === form.answerId
          );
          answer.score = form.score;
          answer.comment = form.comment;
          this.$refs["Appraise"].dialogShow = false;
        } else if (res.Info) {
          this.$message.error(res.Info);
        }
        this.$refs["Appraise"].btnLoading = false;
      }).catch(() => {
        this.$refs["Appraise"].btnLoading = false;
        this.$message.error("")
      });
    },
    getPicture (val) {
      return `${this.$root.PictureURL}?filename=${val}&fileType=${ModulePicture.ScreenSubject}&platFormType=1`;
    }
  }
};
</script>

