<template>
  <div>
    <el-row>
      <el-col :span="12">
        <el-form-item label="">{{Info.RPName}}</el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item label="">
          <span v-if="Info.RPType === 1"></span>
          <span v-if="Info.RPType === 2"></span>
          <span v-if="Info.RPType === 3"></span>
          <span v-if="Info.RPType === 4"></span>
          <span v-if="Info.RPType === 5"></span>
          <span v-if="Info.RPType === 6"></span>
          <span v-if="Info.RPType === 7"></span>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="12">
        <el-form-item label="" v-if="Info.RPType === 7">{{planTime}}</el-form-item>
        <el-form-item
          v-if="Info.RPType !== 7&&isView"
          label=""
        >{{Info.RPStart[0] | FormatDate}}  {{Info.RPStart[1] | FormatDate}}</el-form-item>
        <el-form-item
          v-if="Info.RPType !== 7&&!isView"
          label=""
        >{{Info.RPStart[0] }}  {{Info.RPStart[1] }}</el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item label="">{{Info.RPLive ? "" : ""}}</el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="Info.RPType === 6 ? 12 : 24">
        <el-form-item label="">{{Info.RPTName}}</el-form-item>
      </el-col>
      <el-col :span="12" v-if="Info.RPType === 6">
        <el-form-item label="">
          <span v-if="Info.RPMode === 1"></span>
          <span v-if="Info.RPMode === 2"></span>
          <span v-if="Info.RPMode === 3"></span>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24" v-if="!(Info.RPType === 7 || (Info.RPType === 6 && Info.RPMode !== 1))">
        <el-form-item label="">
          <el-table stripe height="200px" :data="Info.ExItems">
            <el-table-column width="70" property="typeid" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.typeid === 1"></span>
                <span v-if="scope.row.typeid === 2"></span>
                <span v-if="scope.row.typeid === 3"></span>
                <span v-if="scope.row.typeid === 4"></span>
                <span v-if="scope.row.typeid === 5"></span>
                <span v-if="scope.row.typeid === 6"></span>
                <span v-if="scope.row.typeid === 7"></span>
              </template>
            </el-table-column>
            <el-table-column property="name" label="" />
            <el-table-column width="180" property="date" label=""></el-table-column>
          </el-table>
        </el-form-item>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  props: {
    // 
    Info: {
      type: Object,
      default: () => null
    },
    isView: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    planTime () {
      const a = this.Info.RPTName.match(/\d{4}-\d{1,2}-\d{1,2}/);
      const b = this.Info.RPTName.match(
        /\d{1,2}:\d{1,2}:\d{1,2}-\d{1,2}:\d{1,2}:\d{1,2}/
      );

      if (a && b) {
        return a[0] + " " + b[0];
      } else {
        return "--";
      }
    }
  }
};
</script>

<style lang="less">
</style>