<script>
import api from "@/scripts/api";
import Highcharts from "highcharts";
import { FormatDate, getSession } from "@/scripts/utils";

export default {
  name: "Attendance",
  components: {
    Table: () => import("@/components/Table")
  },
  data () {
    return {
      selectedIds: [],
      sid: "sid",
      courseRecord: getSession("campus_CourseRecord"),
      courseRecordID: getSession("campus_CourseRecordID"),

      signTimeList: [],
      signID: "",
      signStats: {
        No: 1,
        Yes: 5,
        Total: 6
      },
      signPersonList: [],

      tableColumns: [
        {
          Code: "studentName",
          Header: "",
          Sort: "custom"
        },
        {
          Code: "signTime",
          Slot: true,
          Header: "",
          Sort: "custom"
        }
      ],
      listLoading: false
    };
  },
  created () {
    this.signTimes();
    this.signStatsGet();
  },
  methods: {
    FormatDate (dateValue, format) {
      return FormatDate(dateValue, format);
    },
    // 
    signTimes () {
      this.signTimeList = [];
      api.ISign.DataTimesList(this.courseRecordID).then(res => {
        if (res.Result) {
          res.Datas.forEach(item => {
            this.signTimeList.push({
              text: `${item["Number"]}`,
              value: item["SNID"],
              number: item["Number"],
              courseID: item["CIID"]
            });
          });
          if (res.Datas.length > 0) {
            this.signID = res.Datas[0]["SNID"];
            this.signPersonListGet();
            this.$refs["table"].MaxTblHeight = 250;
          }
        }
      }).catch(() => {
      });
    },
    signPersonListGet () {
      this.signStatsGet();
      this.signPersonList = [];
      this.listLoading = true;
      api.ISign.TeacherDataList(this.courseRecordID, this.courseRecord.classID, this.signID).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach((item, index) => {
            this.signPersonList.push({
              sid: index,
              SignID: item.Value == null ? "" : item.Value.SignID,
              studentID: item.Key.StudentID,
              studentName: item.Key.StudentName,
              signTime: item.Value == null ? "" : item.Value.SignTime
              // signState: item.Value == null ? -1 : item.Value.SignState
            });
          });
        }
        this.listLoading = false;
      }).catch(() => {
        this.listLoading = false;
      });
    },
    // 
    signStatsGet () {
      api.ISign.DataStatistics(this.signID, this.courseRecord.classID).then(res => {
        if (res.Result) {
          this.signStats = {
            No: res["NoSign"] || 0,
            Yes: res["Sign"] || 0,
            Total: (res["NoSign"] || 0) + (res["Sign"] || 0)
          };
        }
        this.$nextTick(() => {
          this.drawClassManaChart();
        });
      }).catch(() => {
        this.$nextTick(() => {
          this.drawClassManaChart();
        });
      });
    },

    drawClassManaChart () {
      const data = [
        {
          name: "",
          y: this.signStats.Yes
        },
        {
          name: "",
          y: this.signStats.No
        }
      ];
      const legend = [];
      data.forEach((element, i) => {
        legend.push(element.name);
      });
      const options = {
        colors: ["#71C847", "#FA7777", "#2A9EFC", "#EA9855"],
        chart: {
          backgroundColor: null,
          type: "pie"
        },
        credits: { enabled: false },
        title: {
          floating: true,
          useHTML: true,
          text:
            `<div style="text-align:center; width:200px; font-size:14px; color:#aaaaaa;"><span style="font-size:40px; font-family: Digital; color:#333;">${this.signStats.Total}</span><br/></div>`
        },
        legend: {
          floating: true,
          align: "right",
          verticalAlign: "middle",
          x: -50
        },
        plotOptions: {
          pie: {
            allowPointSelect: true,
            cursor: "pointer",
            dataLabels: {
              enabled: false
            },
            showInLegend: true
          }
        },
        tooltip: {
          pointFormat: "{point.y}<br/>{point.percentage:.1f}%"
        },
        series: [{
          name: "",
          innerSize: "80%",
          data: data
        }]
      };
      this.pieChart = new Highcharts.Chart("classManaChart", options, function(c) {
        // 
        var centerY = c.series[0].center[1];
        var centerX = c.series[0].center[0];
        var titleHeight = parseInt(c.title.styles.fontSize);
        // 
        c.setTitle({
          x: centerX - c.title.textWidth / 2 - 20,
          y: centerY + titleHeight / 2 - 20
        });
      });
    }
  }
};
</script>

