<template>
  <div class="record_module" v-loading="dataLoading">
    <div class="module_header">
      <div class="name"></div>
      <div class="more" @click="moreData()">
        
        <i class="el-icon-arrow-right"></i>
      </div>
    </div>

    <div v-loading="dataLoading">
      <el-row :gutter="20" v-if="scorePreview && scorePreview.length > 0">
        <el-col :span="6" v-for="(item, index) in scorePreview" :key="index">
          <el-card shadow="hover" class="list_card blue_bg">
            <el-row :gutter="20">
              <el-col :span="9">
                <img class="card_head" :src="defaultHeadURL" alt/>
                <div>{{ item['Datas']['IsAnonymous'] === 1 ? '' : item.StudentName}}</div>
              </el-col>
              <el-col :span="15" class="card_info">
                <div class="card_mark">
                  <div>
                    {{FormatDate(item['Datas']['SubmitTime'], 'yyyy-MM-dd hh:mm') }}
                    <span class="card_key">{{ item['Datas']['Score'] }}</span> 
                  </div>
                  <p>{{item['Datas']['Comment']}}</p>
                </div>
              </el-col>
            </el-row>
          </el-card>
        </el-col>
      </el-row>
      <div v-else></div>
    </div>

    <el-dialog title="" :visible.sync="dialogTableVisible" :close-on-click-modal="false"
               :modal-append-to-body="false" class="dialog_list">
      <Table ref="table" :ShowTopBar="false" :ShowTblFoot="false" :ShowSelectColumn="false" :ShowExport="false"
             :Columns="tableColumns" :Datas.sync="courseCommentList" :Loading="listLoading" :showMaxeight="false">
        <template slot="StudentName" slot-scope="scope">
          {{scope.row['Datas']['IsAnonymous'] === 1 ? '' : scope.row['StudentName'] }}
        </template>
        <template slot="Score" slot-scope="scope">{{scope.row['Datas']['Score']}}</template>
        <template slot="Comment" slot-scope="scope">{{scope.row['Datas']['Comment']}}</template>
        <template slot="SubmitTime" slot-scope="scope">
          {{FormatDate(scope.row['Datas']['SubmitTime'], 'yyyy-MM-dd hh:mm')}}
        </template>
      </Table>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/scripts/api";
import { FormatDate, getSession } from "@/scripts/utils";

export default {
  name: "CourseComment",
  components: { Table: () => import("@/components/Table") },
  data () {
    return {
      courseRecord: getSession("campus_CourseRecord"),
      courseRecordID: getSession("campus_CourseRecordID"),
      defaultHeadURL: require("@/assets/img/icons/default_head.png"),
      dataLoading: false,
      courseCommentList: [],
      tableColumns: [
        {
          Code: "StudentName",
          Slot: true,
          Header: ""
        },
        {
          Code: "Score",
          Slot: true,
          Header: "",
          sort: true
        },
        {
          Code: "Comment",
          Slot: true,
          Header: ""
        },
        {
          Code: "SubmitTime",
          Slot: true,
          Header: "",
          sort: true
        }
      ],
      listLoading: false,
      dialogTableVisible: false
    };
  },
  created () {
    this.GetCourseComment();
  },
  methods: {
    FormatDate (dateValue, format) {
      return FormatDate(dateValue, format);
    },
    // 
    GetCourseComment () {
      this.courseCommentList = [];
      this.dataLoading = true;
      api.ICourseEval.StudentDataList(this.courseRecordID).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.courseCommentList.push(item);
          });
        }
        this.dataLoading = false;
      }).catch(() => {
        this.dataLoading = false;
      });
    },
    // 
    moreData () {
      this.dialogTableVisible = true;
      setTimeout(() => {
        this.$refs["table"].MaxTblHeight = 500;
      }, 0);
    }
  },
  computed: {
    scorePreview () {
      return this.courseCommentList.filter((r, k) => k < 4);
    }
  }
};
</script>

