  .card_info {
    color: #2c3e50;
    background-color: #fff;
    border-radius: 10px;
    height: 106px;
    overflow-y: auto;
    word-break: break-all;
    word-wrap: break-word;

    .card_mark {
      width: 100%;
      text-align: left;
      font-size: 12px;
      line-height: 1.5;

      div {
        color: #2a9efc;
      }

      .card_key {
        padding-left: 10px;
        color: #f56c6c;
      }

      p {
        font-size: 14px;
