<template>
  <transition>
    <div class="menu_main" :class="{ showmenu: ShowMenu }">
      <aside>
        <el-menu
          :collapse="true"
          menu-trigger="hover"
          :router="true"
          @select="Selected"
          :default-active="this.$route.path"
          @mouseover.native="Open"
        >
          <template v-for="menu in Menu">
            <el-submenu
              :index="menu.ModuleWebURL"
              :popper-append-to-body="false"
              :key="menu.ModuleCode"
              v-if="
                menu.SubModule &&
                  menu.ModuleParameter3 !== '0' &&
                  menu.SubModule.length > 0
              "
            >
              <template slot="title">
                <i>
                  <i class="iconfont" :class="menu.ModuleParameter1"></i>
                  {{ menu.ModuleName }}
                </i>
                <span slot="title">{{ menu.ModuleName }}</span>
              </template>
              <template v-for="subMenu in menu.SubModule">
                <el-submenu
                  :index="subMenu.ModuleWebURL"
                  :key="subMenu.ModuleCode"
                  v-if="
                    subMenu.SubModule &&
                      subMenu.ModuleParameter3 !== '0' &&
                      subMenu.SubModule.length > 0
                  "
                >
                  <span slot="title" class="sub">
                    <i class="iconfont" :class="subMenu.ModuleParameter1"></i>
                    {{ subMenu.ModuleName }}
                  </span>
                  <el-menu-item
                    :index="ssMenu.ModuleWebURL"
                    v-for="ssMenu in subMenu.SubModule"
                    :key="ssMenu.ModuleCode"
                  >
                    <i class="iconfont" :class="ssMenu.ModuleParameter1"></i>
                    {{ ssMenu.ModuleName }}
                  </el-menu-item>
                </el-submenu>
                <el-menu-item
                  :index="subMenu.ModuleWebURL"
                  v-else-if="subMenu.ModuleParameter3 !== '0'"
                  :key="subMenu.ModuleCode"
                >
                  <i class="iconfont" :class="subMenu.ModuleParameter1"></i>
                  {{ subMenu.ModuleName }}
                </el-menu-item>
              </template>
            </el-submenu>
            <el-menu-item
              :index="menu.ModuleWebURL"
              v-else-if="menu.ModuleParameter3 !== '0'"
              :key="menu.ModuleCode"
            >
              <i class="iconfont" :class="menu.ModuleParameter1"></i>
              {{ menu.ModuleName }}
            </el-menu-item>
          </template>
        </el-menu>
      </aside>
      <div class="mask" @click="Close"></div>
    </div>
  </transition>
</template>

<script>
import api from "@/scripts/api";
import store from "@/scripts/store";
import $ from "jquery";
let h = -2;
export default {
  props: {
    ShowMenu: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      Menu: [],
      IsShowMenu: this.ShowMenu,
      h2: 0
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.InitMenu();
    });
  },
  methods: {
    /**
     * 
     */
    Close() {
      this.$emit("update:ShowMenu", false);
    },
    /**
     * 
     */
    async InitMenu() {
      const mp = store.GetMP();
      mp = "hfc";
      if (mp) {
        const res = await api.Module.GetModuleTree();
        console.log(res);
        const isAdmin = store.GetUserInfo().IsSupperAdmin;
        if (res) {
          if (res.Datas && res.Datas.length) {
            for (let i = res.Datas.length - 1; i >= 0; i--) {
              const element = res.Datas[i];
              if (isAdmin || (mp[element.ModuleCode] & 1) === 1) {
                if (element && element.SubModule) {
                  try {
                    const SubModule = JSON.parse(element.SubModule);
                    if (SubModule && SubModule.length > 0) {
                      for (let j = SubModule.length - 1; j >= 0; j--) {
                        const sm = SubModule[j];
                        if (isAdmin || (mp[sm.ModuleCode] & 1) === 1) {
                          if (sm && sm.SubModule && sm.SubModule.length > 0) {
                            for (let k = sm.SubModule.length - 1; k >= 0; k--) {
                              const ssm = sm.SubModule[k];
                              if (isAdmin || (mp[ssm.ModuleCode] & 1) === 1) {
                              } else {
                                sm.SubModule.splice(k, 1);
                              }
                            }
                          }
                        } else {
                          SubModule.splice(j, 1);
                        }
                      }
                    }
                    this.$set(element, "SubModule", SubModule);
                  } catch (ex) {}
                }
              } else {
                res.Datas.splice(i, 1);
              }
            }
          }

          this.Menu = res.Datas;
        }
      }
    },
    /**
     *  
     */
    Selected(index, indexPath) {
      this.Close();
    },
    beforeDestroy() {
      clearTimeout(this.timer);
      this.timer = null;
    },
    Open(e) {
      var top = 0;
      const cname = $(e.target).attr("class");
      let fn = $(e.target)
        .parent()
        .children()[1];
      const wh = window.innerHeight;
      if (cname === "el-submenu__title" || cname === "sub") {
        if (cname === "sub") {
          fn = $(e.target)
            .parent()
            .parent()
            .children()[1];
        }
        this.timer = setTimeout(() => {
          top = $(fn).css("top");
          h = $(fn).children()[0].clientHeight;
          if (top !== "0px" && top !== "auto") {
            top = Number(top.slice(0, -2));
            const ch = top + h + 10;
            if (ch > wh) {
              const newtop = top + wh - ch - 10 + "px";
              $(fn).css("top", newtop);
            }
          }
        }, 500);
      }
    }
  }
};
</script>

<style lang="less">
.menu_main {
  display: flex;
  position: fixed;
  top: @TopBarHeight;
  bottom: 0px;
  left: 0px;
  right: 0px;
  width: 0px;

  aside {
    position: relative;
    z-index: 10;
    min-width: 240px;
    height: 100%;
    transform: translateX(-100%);
    transition: all 0.25s ease-in-out 0s;
  }

  .mask {
    position: absolute;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.2);
    width: 100vw;
    height: 100vh;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.25s ease-in-out 0s;
  }

  .el-menu {
    transform: translateX(-100%);
    .el-submenu {
      * {
        color: @FontColor;
        font-style: normal;
      }
      .el-submenu {
        position: relative;
      }
    }

    .el-menu-item.is-active {
      color: @FontColor;
    }

    .el-submenu__title:hover,
    .el-menu-item:focus,
    .el-menu-item:hover {
      color: #111111;
      background-color: #f5f5f5;
    }
  }

  .el-menu--collapse {
    width: auto;
    > .el-submenu {
      > .el-submenu__title {
        .el-submenu__icon-arrow {
          display: block;
        }
      }
    }
  }
}

.showmenu {
  z-index: 2998;
  aside {
    background-color: rgb(255, 255, 255);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 4px 0px;
    transform: none;
    .el-menu {
      transform: none;
    }
  }

  .mask {
    visibility: visible;
    opacity: 1;
  }
}
</style>
