<template>
  <div class="practice_detail">
    <div class="head">
      <!--        <div class="part_title"></div>-->
      <!--        <el-row class="part_line">-->
      <!--          <el-col :span="6">{{cpdata.CPTitle}}</el-col>-->
      <!--          <el-col :span="6">{{cpdata.SubjectTotal}}</el-col>-->
      <!--          <el-col :span="6">{{cpdata.SubjectScore}}</el-col>-->
      <!--          <el-col :span="6">(){{cpdata.CPTime}}</el-col>-->
      <!--        </el-row>-->

      <el-carousel trigger="click" arrow="always" :autoplay="false" :height="autoHeight" @change="selectData"
                   style="padding:10px 0;">
        <el-carousel-item v-for="item in parseInt(cpdata.SubjectTotal||'0')" :key="item">
          <div class="mb" :ref="'carousel'+item">
            <div class="mb">
              {{psdata.PSSerial}}. [{{fetchSSType(psdata.PSType)}}] {{psdata.PSScore}}
            </div>
            <div class="mb">{{psdata.PSContent}}</div>
            <div class="mb" v-if="psdata.PSType !== 3">
              <div v-for="(item,index) in sodatas" :key="index" :item="item">
                {{item.SOSerial}}. {{item.SOContent}}
              </div>
            </div>
            <div class="mb">
              
              <span class="primary">{{psdata.PSAnswer}}</span>
            </div>
            <div class="mb">
              
              <span class="danger">{{psdata.PSanalysis}}</span>
            </div>
          </div>
        </el-carousel-item>
      </el-carousel>

      <div class="part_title"></div>
      <div class="part_line">
        <el-row v-if="subjectType !== 4">
          <el-col :span="10">
            <r-chart ref="rchart" title="" :data="answerStatistics" @chartChange="selectChart">
            </r-chart>
          </el-col>
          <el-col :span="14">
            <person-card title="" :data="answerPersons" :pictureType="7"></person-card>
          </el-col>
        </el-row>
        <el-row v-else>
          <el-col :span="24">
            <person-card ref="Appraise" showDetail title="" :data="answerList" :subject-type="subjectType"
                         @submitSave="saveAppraise" :pictureType="7">
            </person-card>
          </el-col>
        </el-row>
      </div>

      <div class="part_title"></div>
      <div class="part_line">
        <el-row>
          <el-col :span="2" class="line_left"></el-col>
          <el-col :span="18">
            <el-progress :percentage="surveyInfo.yesPer||0" :text-inside="true" :stroke-width="24" status="success">
            </el-progress>
          </el-col>
          <el-col :span="4" class="line_right">{{surveyInfo.yes}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="2" class="line_left"></el-col>
          <el-col :span="18">
            <el-progress :percentage="surveyInfo.noPer||0" :text-inside="true" :stroke-width="24" status="exception">
            </el-progress>
          </el-col>
          <el-col :span="4" class="line_right">{{surveyInfo.no}}</el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
import RChart from "./RChart";
import PersonCard from "./PersonCard";

import api from "@/scripts/api";
import { practiceTypeName } from "@/scripts/dictionary";

export default {
  components: { RChart, PersonCard },
  props: {
    cpdata: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data () {
    return {
      // cpdata: JSON.parse(sessionStorage.getItem("cpdata")),
      psdata: Object,
      sodatas: [],
      psserial: 1,
      autoHeight: "300px",
      selectIndex: "0",
      subjectId: "",
      subjectAnswer: "",
      subjectType: "",
      answerStatistics: [],
      answerPersons: [],
      answerList: [],
      surveyInfo: {}
    };
  },
  created () {
  },
  methods: {
    // 
    getSubjectData () {
      this.psdatas = [];
      this.sodatas = [];
      api.IClassPractise.SubjectDataInfo(
        this.cpdata.PLID,
        this.psserial
      ).then(res => {
        if (res.Result && res.Data) {
          this.psdata = res.Data;
          this.subjectId = this.psdata["PSID"];
          this.subjectType = this.psdata["PSType"];
          if (res.SODatas && res.SODatas.length > 0) {
            res.SODatas.forEach(item => {
              this.sodatas.push(item);
            });
          }
        }
        this.answerStatistics = [];
        this.answerPersons = [];
        this.answerList = [];
        if (this.subjectType !== 4) {
          this.getAnswerStatistics();
        } else {
          this.getAnswerList();
        }
        this.getSurveyList();
        this.initHeight();
      }).catch(() => {
        this.initHeight();
      });
    },
    getAnswerStatistics () {
      api.IClassPractise.PractiseAnswerStatistics(
        this.cpdata["CIPID"],
        this.subjectId
      ).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          let total = 0;
          res.Datas.forEach(r => {
            total += r["Total"] || 0;
          });
          let active = -1;
          res.Datas.forEach((item, k) => {
            let per = 0;
            if (total !== 0) {
              per = Math.round((10000.0 * (item["Total"] || 0)) / total) / 100;
            }
            const sub = {
              name: item["OptionName"],
              value: item["Total"],
              percentage: per
            };
            this.answerStatistics.push(sub);
            if (active === -1 && (item["Total"] || 0) > 0) active = k;
          });
          if (active > -1 && this.subjectType !== 4) {
            this.$refs["rchart"].selectChart(
              this.answerStatistics[active],
              active
            );
          }
        }
      }).catch(() => {
      });
    },
    getAnswerDetail () {
      this.answerPersons = [];
      api.IClassPractise.PractiseAnswerInfo(
        this.cpdata["CIPID"],
        this.subjectId,
        this.subjectAnswer
      ).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            const sub = {
              id: item.Key["StudentID"],
              userID: item.Key["UserID"],
              name: item.Key["StudentName"],
              gender: item.Key["Gender"],
              no: item.Key["Sno"]
            };
            this.answerPersons.push(sub);
          });
        }
      }).catch(() => {
      });
    },
    getAnswerList () {
      this.answerList = [];
      api.IClassPractise.PractiseAnswerMember(
        this.cpdata["CIPID"],
        this.subjectId
      ).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            const sub = {
              id: item.Key["StudentID"],
              userID: item["UserID"],
              name: item["StudentName"],
              time: item.Key["SubmitTime"],
              score: item.Key["PAScore"],
              type: item.Key["PAForm"],
              answerId: item.Key["PAID"],
              content: item.Key["PAContent"],
              comment: item.Key["PAComment"]
            };
            this.answerList.push(sub);
          });
        }
      }).catch(() => {
      });
    },
    getSurveyList () {
      this.surveyInfo = {
        yes: 0,
        yesPer: 0,
        no: 0,
        noPer: 0,
        total: 0
      };
      api.IClassPractise.PractiseSurveyStatistics(
        this.cpdata["CIPID"],
        this.subjectId
      ).then(res => {
        if (res.Result) {
          this.surveyInfo = {
            yes: res["YesMaster"] || 0,
            yesPer: 0,
            no: res["NoMaster"] || 0,
            noPer: 0,
            total: 0
          };
          this.surveyInfo.total = this.surveyInfo.yes + this.surveyInfo.no;
          this.surveyInfo.yesPer = Math.round((this.surveyInfo.yes * 100.0) / this.surveyInfo.total);
          this.surveyInfo.noPer = Math.round((this.surveyInfo.no * 100.0) / this.surveyInfo.total);
        }
      }).catch(() => {
      });
    },

    // 
    fetchSSType (val) {
      return practiceTypeName(val);
    },
    selectData (val) {
      this.psserial = val + 1;
      this.getSubjectData();
    },
    initHeight () {
      setTimeout(() => {
        if (!this.$refs["carousel" + this.psserial]) {
          this.autoHeight = 300 + "px";
        } else {
          this.autoHeight = this.$refs["carousel" + this.psserial][0].clientHeight + "px";
        }
      }, 200);
    },
    selectChart (index) {
      this.subjectAnswer = this.answerStatistics[index].name;
      this.getAnswerDetail();
    },
    saveAppraise (form) {
      this.$refs["Appraise"].btnLoading = true;
      api.IClassPractise.PractiseScoreSave(
        form.answerId,
        form.score,
        form.comment
      ).then(res => {
        if (res.Result) {
          this.$message({ message: "", type: "success" });
          const answer = this.answerList.find(r => r.answerId === form.answerId);
          answer.score = form.score;
          answer.comment = form.comment;
          this.$refs["Appraise"].dialogShow = false;
        } else if (res.message) {
          this.$message({ showClose: true, ...res });
        }
        this.$refs["Appraise"].btnLoading = false;
      }).catch(() => {
        this.$refs["Appraise"].btnLoading = false;
      });
    }
  }
};
</script>

