<template>
  <div class="person_card">
    <p class="title">{{title}}</p>

    <div class="list">
      <ul v-if="data.length>0" :style="{maxHeight: (height+(showDetail?40:0)) +'px'}">
        <li v-for="(item, index) in data" :key="index" @click="selectItem(item, index)">
          <div class="list_card">
            <img src="" v-real-img="[item.userID, item.id]" alt/>
            <p>{{item.name}}</p>
            <p class="list_line" v-if="showDetail">
              <span v-if="!item.score && item.score!==0"></span>
              <span v-else>{{item.score||0}}</span>
            </p>
          </div>
        </li>
      </ul>
      <div v-else style="text-align: center;"></div>
    </div>

    <el-dialog title="" :visible.sync="dialogShow" width="800px" :modal="false" center append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="">
          <el-input v-if="subjectType!==4" type="textarea" :autosize="{ minRows: 1, maxRows: 5}" disabled
                    v-model="answer">
          </el-input>
          <div v-else class="form_html">
            <div v-html="answer"></div>
          </div>
        </el-form-item>
        <el-form-item label="" prop="comment">
          <el-input type="textarea" :autosize="{ minRows: 1, maxRows: 5}" v-model="form.comment"></el-input>
        </el-form-item>
        <el-form-item label="" prop="score">
          <el-input-number v-model="form.score" :min="0" :max="150" controls-position="right" :precision="0"
                           :step="1">
          </el-input-number>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogShow = false" style="padding: 7px 15px;"> </el-button>
        <el-button v-loading="btnLoading" type="primary" @click="submitForm" style="padding: 7px 15px;"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
// import api from "@/utils/api";

// import { ModulePicture } from "@/scripts/dictionary";

export default {
  name: "",
  props: {
    showDetail: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    },
    height: {
      type: Number,
      default: 300
    },
    subjectType: {
      type: Number,
      default: 1
    },
    pictureType: {
      type: Number
    },
    subScore: {
      type: Number,
      default: 0
    },
    data: {
      type: Array,
      default: () => {
        return [
          {
            name: "A",
            photo: ""
          },
          {
            name: "B",
            photo: ""
          },
          {
            name: "C",
            photo: ""
          },
          {
            name: "D",
            photo: ""
          },
          {
            name: "E",
            photo: ""
          }
        ];
      }
    }
  },
  data () {
    return {
      series: [60, 50, 40, 30, 20, 10, 0],
      selectIndex: 0,
      dialogShow: false,
      btnLoading: false,
      answerType: 1,
      answer: "",
      form: {
        answerId: "",
        comment: "",
        score: 0
      },
      rules: {
        comment: {
          min: 0,
          max: 1000,
          message: " 1000 ",
          trigger: "blur"
        },
        score: {
          required: true,
          message: "",
          trigger: "blur"
        }
      }
    };
  },
  methods: {
    selectItem (item, index) {
      this.selectIndex = index;
      if (this.showDetail) {
        this.answerType = item.type;
        if (item.content !== null) {
          const htmlstr = item.content;
          this.answer = htmlstr.replace(/<img [^>]*src=['"]([^'"]+)[^>]*>/gi, (match, capture) => {
            const filename = capture.split("/")[1];
            return `<img src="${this.$root.PictureURL}?filename=${filename}&fileType=${this.pictureType}&platFormType=1" alt="" style="max-width:380px"/>`;
          });
        }
        this.form.answerId = item.answerId;
        this.form.comment = item.comment;
        this.form.score = item.score;
        this.dialogShow = true;
      }
    },
    submitForm () {
      this.$refs["form"].validate(valid => {
        if (valid) {
          this.$emit("submitSave", this.form);
        } else {
          this.$message({
            showClose: true,
            message: "",
            type: "error"
          });
          return false;
        }
      });
    }
  }
};
</script>
