<template>
  <el-tooltip content="" :placement="TooltipPlacement">
    <el-select
      filterable
      v-model="Value"
      :placeholder="placeholder"
      :multiple="multiple"
      :allow-create="ArrowCreate"
      @change="ChangeHandle"
      remote
      clearable
      :remote-method="remoteMethod"
      reserve-keyword
      :loading="loading"
      collapse-tags
      :size="size"
    >
      <el-option
        v-for="item in Datas"
        :key="item.CourseID"
        :label="item.CourseName"
        :value="item.CourseID"
      ></el-option>
    </el-select>
  </el-tooltip>
</template>

<script>
import api from "@/scripts/api";

export default {
  props: {
    size: {
      type: String,
      default: "mini",
    },
    /**
     * 
     */
    multiple: {
      type: Boolean,
      default: false,
    },
    value: {},
    text: {
      type: String,
      default: "",
    },
    TooltipPlacement: {
      type: String,
      default: "right",
    },
    /**
     * 
     */
    ArrowCreate: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: "",
    },
  },
  watch: {
    async value(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.loading = true;
        const res = await api.Course.GetDataByName(
          "",
          this.multiple ? JSON.stringify(newVal) : `["${newVal}"]`
        );
        if (res) {
          this.Datas = res.Datas;
          this.Value = newVal;
        }
        this.loading = false;
      }
    },
  },
  data() {
    return { loading: false, Value: this.value, Datas: [] };
  },
  mounted() {
    this.LoadData();
  },
  methods: {
    ChangeHandle(val) {
      this.$emit("update:value", val);
      this.$emit("Filter", this.Value);
      const findC = this.Datas.find((c) => c.CourseID === val);
      if (findC) this.$emit("update:text", findC.CourseName);
      else {
        this.$emit("update:text", val);
      }
    },
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        this.LoadData(query);
      } else {
        this.Datas = [];
      }
    },
    async LoadData(name) {
      const res = await api.Course.GetDataByName(
        name,
        this.multiple ? JSON.stringify(this.Value) : `["${this.Value}"]`
      );
      if (res) {
        this.Datas = res.Datas;
      }
      this.loading = false;
    },
  },
};
</script>

