<template>
    <el-cascader v-model="Value" :placeholder="placeholder" :options="options" :props="oprops" :show-all-levels="false" ref="cascader" :size="size"
        clearable @change="ChangeHandle" collapse-tags>
        <template slot-scope="{ node, data }">
            <span>{{ data.ATName }}</span>
        </template>
    </el-cascader>
</template>

<script>
    import api from "@/scripts/api";
    export default {
        props: {
            size: {
                type: String,
                default: "mini"
            },
            value: {},
            placeholder: {
                type: String,
                default: ""
            },
            Values: {
                type: Array,
                default: () => []
            }
        },
        watch: {
            value(newVal, oldVal) {
                if (newVal !== oldVal) {
                    this.Value = newVal;
                    this.$nextTick(() => {
                        this.LoadTreeData(0, null, this);
                    });
                }
            }
        },
        data() {
            return {
                Value: this.value,
                options: [],
                oprops: {
                    emitPath: false,
                    value: "ATID",
                    id: "ATID",
                    label: "ATName",
                    multiple: true,
                    children: "children",
                    expandTrigger: "hover",
                    checkStrictly: true
                }
            };
        },
        mounted() {
            this.LoadTreeData(0, null, this);
        },
        methods: {
            ChangeHandle(val) {
                this.$emit("update:value", this.Value);
              //  this.$emit("Filter", this.Value);
                const checkedNodes = this.$refs.cascader.getCheckedNodes();
                if (checkedNodes && checkedNodes.length > 0) {
                    this.$emit(
                        "update:Values",
                        this.$refs.cascader.getCheckedNodes()[0].path
                    );
                } else {
                    this.$emit("update:Values", []);
                }

                const LZString = require("lz-string");
                const jsonStr = JSON.stringify(val);
                let result = "";
                if (jsonStr) {
                    result = LZString.compressToBase64(
                        jsonStr.slice(1, jsonStr.length - 1)
                    );
                }
                this.$emit("Filter", result);
            },
            /**
             * 
             */
            async LoadTreeData(level, pid, parent) {
                const res = await api.AssetType.GetAllDatas();
                if (res) {
                    var atnm = this.toTree(res.Datas, "ATID", "ATParentID", "ATName")
                    this.options = atnm
                    // this.$set(parent, "options", atnm);
                }
            },
            toTree(data, id, pid, name) {
                //   children,
                data.map(item => {
                    item.vlaue = item[id];
                    item.label = item[name]
                })
                //   id  KEY  map 
                var map = {};
                data.forEach(function (item) {
                    map[item[id]] = item;
                });
                var val = [];
                data.forEach(function (item) {
                    // pid,mapid
                    var parent = map[item[pid]];
                    // ,
                    if (parent) {
                        (parent.children || (parent.children = [])).push(item);
                    } else {
                        // mapID, item val
                        val.push(item);
                    }
                });
                return val;
            }
        }
    }
</script>