<template>
  <div class="sub_module">
    <div class="module_header">
      <div class="title"></div>
      <div class="more" @click="initDialog()">
        
        <i class="el-icon-arrow-right"></i>
      </div>
    </div>

    <div v-loading="dataLoading">
      <el-row :gutter="20" v-if="dataList && dataList.length > 0">
        <el-col :span="6" v-for="(item, index) in dataList" :key="index">
          <el-card shadow="hover" class="list_card blue_bg">
            <div class="card_info" @click="initDialog(item['Type'])">
              <div>{{item.name}}</div>
              <div class="card_line">
                <div>{{item['Total']}}</div>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <div v-else></div>
    </div>

    <el-dialog title="" :visible.sync="dialogVisible" :close-on-click-modal="false" :modal-append-to-body="false">
      <el-tabs type="border-card" v-model="selectType" @tab-click="selectTypeChange">
        <el-tab-pane v-for="(item, index) in dataList" :key="index" :label="item.name" :name="item['Type'] + ''">
          <keep-alive>
            <el-table :data="typeDataList" max-height="550" v-loading="listLoading" stripe type="expand"
                      :default-sort="{prop: 'SSSerial', order: 'ascending'}">
              <el-table-column type="expand">
                <template slot-scope="props">
                  <screen-detail :cpdata="props.row" :subjectId="props.row['SSID']+''"></screen-detail>
                </template>
              </el-table-column>
              <el-table-column property="SSSerial" label=""></el-table-column>

              <el-table-column property="SSPatternName" label="">
                <template slot-scope="scope">
                  <span>{{getPatternName(scope.row.SSPattern)}}</span>
                </template>
              </el-table-column>

              <el-table-column property="SSScore" label=""></el-table-column>

              <el-table-column property="SSTime" label="()"></el-table-column>

              <el-table-column property="CreateTime" label="" width="150px">
                <template slot-scope="scope">
                  <span>{{formatDate(scope.row.CreateTime, 'yyyy-MM-dd hh:mm:ss')}}</span>
                </template>
              </el-table-column>

              <el-table-column property="State" label="">
                <template slot-scope="scope">
                  <span>{{getStateName(scope.row.State)}}</span>
                </template>
              </el-table-column>
            </el-table>
          </keep-alive>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/scripts/api";
import { FormatDate, getSession } from "@/scripts/utils";
import { practiceTypeName, screenPatternName, screenSubjectStateName } from "@/scripts/dictionary";

export default {
  name: "ScreenQuestion",
  components: {
    ScreenDetail: () => import("./ScreenDetail")
  },
  data () {
    return {
      courseRecord: getSession("campus_CourseRecord"),
      courseRecordID: getSession("campus_CourseRecordID"),
      dataList: [],
      dataLoading: false,

      dialogVisible: false,
      selectType: null,
      listLoading: false,
      typeDataList: []
    };
  },
  created () {
    this.getScreenQuestionStats();
  },
  methods: {
    // 
    getScreenQuestionStats () {
      this.dataLoading = true;
      this.dataList = [];
      api.IScreenSubject.DataOptionStatistics(this.courseRecordID).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.dataList.push({
              ...item,
              name: this.screenQuestionTypeName(item["Type"])
            });
          });
        }
        this.dataLoading = false;
      }).catch(() => {
        this.dataLoading = false;
      });
    },
    initDialog (type) {
      if (type) {
        this.selectType = type + "";
      } else if (this.dataList.length > 0) {
        this.selectType = this.dataList[0]["Type"] + "";
      }
      this.selectTypeChange();
      this.dialogVisible = true;
    },
    // 
    selectTypeChange () {
      this.listLoading = true;
      this.typeDataList = [];
      api.IScreenSubject.DataList(this.courseRecordID, this.selectType).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.typeDataList.push(item);
          });
        }
        this.listLoading = false;
      }).catch(() => {
        this.listLoading = false;
      });
    },

    getPatternName (val) {
      return this.screenPatternName(val);
    },
    formatDate (dateValue, format) {
      return FormatDate(dateValue, format);
    },
    getStateName (val) {
      return this.ScreenSubjectStateName(val);
    },
    /**
     * 
     * @param {*} val
     */
    screenQuestionTypeName (val) {
      return practiceTypeName(val);
    },

    /**
     * 
     * @param {*} val
     */
    screenPatternName (val) {
      return screenPatternName(val);
    },

    /**
     * 
     */
    ScreenSubjectStateName (val) {
      return screenSubjectStateName(val);
    }
  }
};
</script>

