<template>
  <div class="chart_main">
    <p class="title">{{title}}</p>

    <div class="table">
      <div v-if="xSeries" class="series">
        <div v-for="(item, index) in series" :key="index">{{item}}</div>
      </div>

      <div class="content">
        <ul class="chart" :style="{height: height +'px'}">
          <li v-for="(item, index) in data" :key="index" :style="{width: (100.0/data.length)+'%'}"
              @click="selectChart(item, index)">
            <!-- <span class="chart_value">{{item.value}}</span> -->
            <span>{{item.percentage}}%</span>
            <div class="chart_line" :class="selectIndex===index?'active':''"
                 :style="{height: height * item.percentage/100.0  +'px'}"></div>
            <!-- <span>{{item.name}}</span> -->
          </li>
        </ul>

        <ul class="remark">
          <li v-for="(item, index) in data" :key="index" :style="{width: (100.0/data.length)+'%'}">
            <span>{{item.name}}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  props: {
    title: {
      type: String,
      default: ""
    },
    xSeries: {
      type: Boolean,
      default: false
    },
    height: {
      type: Number,
      default: 300
    },
    data: {
      type: Array,
      default: () => {
        return [
          { name: "A", value: 42, percentage: 75 },
          { name: "B", value: 8, percentage: 10 },
          { name: "C", value: 6, percentage: 6 },
          { name: "D", value: 4, percentage: 5 },
          { name: "E", value: 2, percentage: 2 }
        ];
      }
    }
  },
  data () {
    return {
      //   title: "",
      series: [60, 50, 40, 30, 20, 10, 0],
      //   dataList: [
      //     { name: "A", value: 42, percentage: 75 },
      //     { name: "B", value: 8, percentage: 10 },
      //     { name: "C", value: 6, percentage: 6 },
      //     { name: "D", value: 4, percentage: 5 },
      //     { name: "E", value: 2, percentage: 2 }
      //   ],
      selectIndex: 0
    };
  },
  methods: {
    selectChart (item, index) {
      this.selectIndex = index;
      this.$emit("chartChange", index);
    }
  }
};
</script>

